export default class InstagramView extends Component {
    render() {
        const { hashTag } = this.props
        return (
            <div className='row mt-3 '>
                <div className='col-sm-4  '>
                    <BlackBlock className=''>
                        <a href='https://www.instagram.com/cleantonn/'>
                            <NadpisWhite>  </NadpisWhite>
                            <NadpisBlue>
                                 instagram
                            </NadpisBlue>
                        </a>
                    </BlackBlock>
                </div>
                <InstagramFeed hashTag={hashTag} id='hashtagPhotos2' />
                <InstagramFeed hashTag={'cleantonn_'} id='hashtagPhotos' />
            </div>
        )
    }
}
InstagramView.propType = {
    hashTag: PropTypes.string
}


