import React, {Component} from 'react'
import {connect} from 'react-redux'
import {getService} from '../../reducers/Service/reducer'
import UnkownPage from '../404';

import CustomService from '../../components/Service/CustomService';
import Service from '../../components/Service/Service';

export class ServicePage extends Component {

    render() {
        if (this.props.Service && !this.props.Service.isCustom) {
            return <Service Service={this.props.Service}></Service>

        } else if (this.props.Service && this.props.Service.isCustom) {
            return < CustomService Service = {
                this.props.Service
            } > </CustomService>

        } else {
            return <UnkownPage/>
        }
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        Service: getService(ownProps.type, state)
    }
}

export default connect(mapStateToProps)(ServicePage)


