import imutable from 'seamless-immutable'

const initialState = imutable([
    {
        to: '/',
        name: '',
        description: ',  , , ,   , ',
        keywords: 'cleantonn'
    }, {
        isDropdown: true,
        name: '',
        DropdownItems: [
            {
                to: '/service/wash',
                alt: ' ',
                name: ''
            }, {
                to: '/service/himchistka',
                alt: ' ',
                name: ''
            }, {
                to: '/service/polish',
                alt: ' ',
                name: ''
            }, {
                to: '/service/nano',
                alt: '',
                name: ''
            }, {
                to: '/service/paint',
                alt: ' ',
                name: ' '
            }, {
                to: '/service/repair',
                alt: ' ',
                name: ' , '
            }
        ]
    }, {
        to: '/advantages',
        name: ' ',
        description: '        !!!',
        keywords: 'customKeywords'
    }, {
        to: '/rewiews',
        name: ' '
    },
    /*{
        to: '/offers',
        name: ''
    },*/
    {
        to: '/contact',
        name: ' '
    }
])
export const Navigation = (state = initialState, action) => {
    switch (action.type) {

        default:
            return state
    }
}
export function getNavigation(state) {
    return state.Navigation
}

export function getSeoData(location, state) {
    return state
        .Navigation
        .map((v) => {
            if (v.isDropdown) 
                return v.DropdownItems.filter((v) => v.to === location)
            else if (v.to === location) 
                return v
            else 
                return undefined
        })
        .filter((v) => v && v.to
            ? true
            : false)[0]
}

export function getServiceInfo(state) {
    return state
        
        .Navigation
        .filter((val) => val.isDropdown)[0]
        .DropdownItems
}


