import React, {Component} from 'react'
import But from '../../components/Advantages/But'
import {getAdvantages} from '../../reducers/Advantages/reducer'
import {connect} from 'react-redux'
import './../../components/BlackBlock.css'
export class Buts extends Component {
    render() {
        const {Advantages} = this.props
        return (
            <div className='row '>
                <div className='container mt-3'>
                    <div className='borderr '>
                        <ul className='font_7 m-2'>
                            {Advantages.map((val, i) => (<But key={i} main={val.main} but={val.but}/>))}
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state, ownProps) => ({Advantages: getAdvantages(state)})

export default connect(mapStateToProps)(Buts)


