import React, {Component} from 'react'

export class NadpisBlue extends Component {
    render() {
        return (
            <div
                className='h2 font_2 color2'
                style={{
                marginBottom: '0px'
            }}>
                {this.props.children}</div>
        )
    }
}
export class NadpisWhite extends Component {
    render() {
        return (
            <div
                className='h2 font_2 text-white'
                style={{
                marginBottom: '0px'
            }}>
                <span>{this.props.children}</span>
            </div>
        )
    }
}



