import React, {Component} from 'react'
import BlackBlock from '../BlackBlock';

export default class Lozung extends Component {
    render() {
        return (

            <div className='col-sm-3'>
                <BlackBlock >
                    <div
                        className='h2 font_2 text-white'
                        style={{
                        marginBottom: '0px',
                        lineHeight:'0.98em'
                    }}>
                        

                        <span className='color2 '> </span>
                        </div>
                </BlackBlock>
            </div>
        )
    }
}



