export default class Order extends Component {
    render() {
        const { price, order } = this.props

        return (
            <div className={'col-sm-3 order-' + order}>
                <BlackBlock>
                    <a href='tel:+78412306806'>
                        <NadpisBlue>{' ' + price + ' .'}</NadpisBlue>
                        <NadpisWhite></NadpisWhite>
                    </a>
                </BlackBlock>
            </div>
        )
    }
} 

Order.propType = {
    price: PropTypes.string.isRequired,
    order: PropTypes.number.isRequired
}


