import React, {Component} from 'react'
import {connect} from 'react-redux'
import { withRouter } from 'react-router-dom';
import '../../components/Header/Nomer.css'
import ListItemLink from '../../components/Header/ListLinkItem'
import {getNavigation} from '../../reducers/Navigation/reducer'

import DropdownItems from '../../components/Header/DropdownItems';


export class NavBar extends Component {
    render() {
        const { navigation } = this.props

        return (
            <div className='collapse navbar-collapse' id="navbarText">
                <ul className='navbar-nav mx-auto'>
                    {navigation.map((val, i) => {
                        if (val.isDropdown) {
                            return (<DropdownItems key={i} togleName={val.name} items={val.DropdownItems} />)
                        } else {
                            return (
                                <ListItemLink key={i} to={val.to}>{val.name}</ListItemLink>
                            )
                        }
                    })}
                </ul>
                <a href='tel:+78412306806' className='navbar-text color-grey font_2 ml-auto '>
                    +7 (8412)
                        <strong
                        style={{
                            fontSize: '38px',
                            color: 'black'
                        }}>306-806</strong>
                </a>
            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => ({navigation: getNavigation(state)})

export default withRouter(connect(mapStateToProps)(NavBar))



