import React, {Component} from 'react'
import {connect} from 'react-redux'
import {NavLink} from 'react-router-dom'
import classNames from 'classnames'
import './ServiceMain.css'
import Photo from '../../images/serviceMain.jpg'
import {getServiceInfo} from '../../reducers/Navigation/reducer'
import {platform} from '../../utils/platform';
import MetaTags from '../MetaTags';
export class ServiceMain extends Component {
    render() {
        const {Info: info} = this.props
        const isMobile = platform()
        const pk = (
            <div
                className='mt-2 mx-auto  d-flex image '
                style={{
                backgroundImage: `url(${Photo})`,
                width: '70%'
            }}>
                <MetaTags />

                <div className='row mt-auto mx-auto w-100  '>
                    {info.map((val, i) => <div className='col-lg-4 col-6 mb-1' key={i}>
                        <div className='text h4 p-3 border-3px-white'>
                            <NavLink
                                id='selector'
                                className='text-white'
                                style={{
                                whiteSpace: 'nowrap'
                            }}to={val.to}>{val.alt}</NavLink>
                        </div>
                    </div>)}
                </div>
            </div>
        )
        const mobile = (
            <div className='container mt-4'>
                <MetaTags/>
                <div className='row'>
                    <div className='col-12 '>
                        <img className='mx-auto img-fluid' src={Photo} alt=''></img>
                    </div>
                    <div className='row mt-2 mx-auto w-100 '>
                        {/* */}

                        {info.map((val, i) => <div className='col-lg-4 col-12 mt-1 mb-1' key={i}>
                            <div
                                className={classNames('text ', {
                                'h6 ': isMobile
                            }, {
                                'h4 p-3 border-3px-white': !isMobile
                            })}>
                                <NavLink
                                    id='selector'
                                    className='text-white'
                                    style={{
                                    whiteSpace: 'nowrap',
                                    fontSize: '30px'
                                }}
                                    to={val.to}>{val.alt}</NavLink>
                            </div>
                        </div>)}

                    </div>
                </div>
            </div >
        )
        return isMobile
            ? mobile
            : pk
    }
}
const mapStateToProps = (state, ownProps) => ({Info: getServiceInfo(state)})

export default connect(mapStateToProps)(ServiceMain)


