export default class Slogan extends Component {
    render() {
        const { data } = this.props
        return (
            <div className='col-sm '>

                <BlackBlock>
                    {data.map((val, i) => {
                        if (val.isBlue) {
                            return <NadpisBlue key={i}>{val.text}</NadpisBlue>
                        } else {
                            return <NadpisWhite key={i}>{val.text}</NadpisWhite>
                        }
                    })}
                </BlackBlock></div>
        )
    }
}

Slogan.propType = {
    data: PropTypes
        .arrayOf(PropTypes.shape({isBlue: PropTypes.bool, text: PropTypes.string.isRequired}).isRequired,)
        .isRequired
}


