import React, {Component} from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
export default class CarouselItem extends Component {
    render() {
        const {itemLink, isActive} = this.props
        return (
            <div className={classNames('carousel-item', {'active': isActive})}>
                <img className="d-block w-100" src={itemLink} alt="Second slide"/>
            </div>
        )
    }
}

CarouselItem.propType = {
    itemLink: PropTypes.string.isRequired,
    isActive: PropTypes.bool
}


