import React, {Component} from 'react'
import Usluga from './Usluga';
import Slogan from './Slogan';
import About from './About';
import InstagramView from '../Instagram/InstagramView';

export default class Service extends Component {
    render({
        ...res
    }) {
        const {items, slogan, about, hashTag} = this.props.Service

        return (
            <div className='container mt-4' {...res}>
                
                <div className='row'>
                    
                    <Slogan data={slogan}></Slogan>
                    <About about={about}></About>
                </div>

                    {items.map((val, i) => < Usluga value = {
                        val
                    }
                    key = {
                        i
                    } />)}
                <InstagramView hashTag={hashTag}/>
            </div>

        )
    }
}



