import React, {Component} from 'react'
import BlackBlock from '../../components/BlackBlock';
import {NadpisWhite, NadpisBlue} from '../../components/Nadpisi';
import MetaTags from '../MetaTags';

export default class Contact extends Component {
    render() {
        return (
            <div className='container mt-4'>
                <MetaTags />

                <div className='row '>
                    <div className='col-sm-4'>
                        <BlackBlock>
                            <NadpisWhite>,</NadpisWhite>
                            <NadpisWhite>,</NadpisWhite>
                            <NadpisBlue></NadpisBlue>
                        </BlackBlock>
                        
                    </div>
                    <div className='col-sm-8'>
                        <iframe
                            frameBorder="no"
                            style={{
                            border: '1px solid #a3a3a3'
                            }} height='600px' title='map' className='w-100'
                            src="https://widgets.2gis.com/widget?type=firmsonmap&amp;options=%7B%22pos%22%3A%7B%22lat%22%3A53.224134%2C%22lon%22%3A45.001141%2C%22zoom%22%3A16%7D%2C%22opt%22%3A%7B%22city%22%3A%22penza%22%7D%2C%22org%22%3A%225911502791909867%22%7D"></iframe>
                    </div>
                </div>
            </div>
        )
    }
}



